.wrapper
{
    height: 150px;
    width: 150px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
}
.L
{
    width: calc(100% - 9px);
    height: calc(100% - 9px);
    border-radius: 50%;
    position: absolute;
    border-top: 5px solid darkblue;
    animation:spin 3s ease-out infinite;
}
@keyframes spin{
    20%{
        width: calc(100% - 19px);
    }
    100%
    {
        transform: rotate(-360deg);
    }
}