  .input101
  {
    font-family: Poppins-Regular;
    font-size: 15px;
    color:black;
    
    border: double lightgrey 2px;
    
    width: 20%;
    height: 50px;
    margin: 10px;
    padding: 5px 5px;
  }
  @media screen and (max-width: 680px) {
   table {
     border: 0;
     display: block;
     box-shadow: none;
     padding: 5px;
   }
  
   thead {
     position: absolute;
     opacity: 0;
   }
    
   tbody {
     display: block;
     min-width: 19em;
     max-width: 25em;
   }
  
   tr {
     border-top: 2px solid black;
     border-bottom: 2px solid black;
     display: block;
     margin-bottom: 1em;
   }
  
   td {
     border-bottom: 1px solid black;
     display: flex;
   }
  
   td::before {
     display: inline-block;
     font-weight: bold;
     margin-right: auto;
     padding-right: 1em;
   }
  
   td:last-child {
     border-bottom: 0;
   }
  }