/* -----------------------------------------
  =Default css to make the demo more pretty
-------------------------------------------- */


.content {
    background-color: lightgray;
    padding: 0px;
    height: 600px;
    overflow: hidden;
    

}
.load-4 .ring-1 {animation: loadingD 2.0s .3s cubic-bezier(.17,.37,.43,.67) infinite;}
@keyframes loadingD {
    0 {transform: rotate(0deg);}
    50% {transform: rotate(180deg);}
    100% {transform: rotate(360deg);}
}

/* -----------------------------------------
  =CSS3 Loading animations
-------------------------------------------- */

/* =Elements style
---------------------- */
.ring-1 {
    width: 150px;
    height: 150px;
    margin: auto;
    margin-top:220px;
    padding: 10px;
    border: 15px dashed #900C3F;
    border-radius: 100%;
}

